<template>
  <div :class="`kg-captcha-${id}`" style="display: flex; justify-content: center; align-items: center"></div>
</template>

<script>
import "@/scripts/captcha.js";
export default {
  name: "KgCaptcha",
  props: ["id", "handler", "reload"],
  mounted() {
    if (!this.id || !this.handler) {
      return;
    }
    window.kg.captcha({
      bind: `.kg-captcha-${this.id}`,
      success: (e) => this.handler(e, "success"),
      failure: (e) => this.handler(e, "failure"),
      refresh: (e) => this.handler(e, "refresh"),
    });
  },
  methods: {
    reset() {
      window.kg.reload({ bind: `.kg-captcha-${this.id}`, success: (e) => this.handler(e, "success"), failure: (e) => this.handler(e, "failure"), refresh: (e) => this.handler(e, "refresh") });
    },
  },
  watch: {
    reload(value) {
      if (value) {
        this.reset();
      }
    },
  },
};
</script>

<style></style>
